<template>
  <div class="detail">
    <header>
      <div class="header-returns" v-show="isShow">
        <i class="iconfont icon-fanhui" @click="backNextPage"></i>
        <i class="iconfont icon-zhuye"></i>
      </div>
      <div class="header-bar" v-show="!isShow" :style="styleOption">
        <i class="iconfont icon-fanhui" @click="backNextPage"></i>
        <span class="heightSpan">商品详情</span>
        <span>商品评价</span>
        <i class="iconfont icon-zhuye"></i>
      </div>
    </header>
    <section ref="wrapper">
      <div>
        <div class="swiper">
          <swiper :options="swiperOption" class="swiper-main">
            <swiper-slide v-for="item in swiperList" :key="item.id">
              <img :src="item.imgUrl" id="swiper-img"
            /></swiper-slide>
          </swiper>
          <div class="swiper-pagination"></div>
        </div>
        <div class="goods-name">
          <h1>{{ newSwiperList[0]?.name }}</h1>
          <span>{{ newSwiperList[0]?.recommed }} </span>
        </div>
        <div class="goods-price">
          <div class="oprice">
            <span>￥</span><b>{{ newSwiperList[0]?.price }}</b>
          </div>
          <div class="pprice">
            <span>原价:</span>
            <s>￥349</s>
          </div>
        </div>
        <div class="goode-img">
          <h4>商品展示</h4>
          <img
            :src="newSwiperList[0]?.imgUrl"
            :alt="newSwiperList[0]?.content"
          />
          <img
            src="https://img.tea7.com/0010994_0.png?x-oss-process=image/resize,w_800"
            alt=""
          />
          <img
            src="https://img.tea7.com/0159004_0.png?x-oss-process=image/resize,w_800"
            alt=""
          />

          <img
            src="https://img.tea7.com/0010993_0.jpeg?x-oss-process=image/resize,w_800"
            alt=""
          />
        </div>
      </div>
    </section>

    <footer>
      <ul>
        <li><i class="iconfont icon-erji"></i><span>客服</span></li>
        <li>
          <i class="iconfont icon-gouwugouwuchedinggou"></i><span>购物车</span>
        </li>
        <li><i class="iconfont icon-shoucang"></i><span>收藏</span></li>
        <li class="addShopCat" @click="addShopCat"><span>加入购物车</span></li>
        <li class="buyNow"><span>立即购买</span></li>
      </ul>
    </footer>
  </div>
</template>

<script>
import BScroll from "better-scroll"; //页面滚动插件
import { getGoodslistId } from "@/api/list"; //调用查询商品id的接口
import { addShopCart } from "@/api/shopCart"; //加入购物车的接口
import { Toast } from "vant";
export default {
  name: "detail-vue",
  data() {
    return {
      styleOption: {
        opacity: 0,
      },
      Bodyscroll: null,
      isShow: true,
      swiperOption: {
        autoplay: 3000,
        speed: 1000,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
          type: "fraction",
        },
      },
      swiperList: [
        {
          id: 0,
          imgUrl:
            "https://img.tea7.com/0010445_0.jpeg?x-oss-process=image/resize,w_800",
        },
        {
          id: 1,
          imgUrl:
            "https://img.tea7.com/0011816_0.jpeg?x-oss-process=image/resize,w_500",
        },
        {
          id: 2,
          imgUrl:
            "	https://img.tea7.com/0012514_0.jpeg?x-oss-process=image/resize,w_800",
        },
      ],
      newSwiperList: [],
      goodsId: -1,
    };
  },
  created() {
    this.goodsId = this.$route.query.id;
    this.getGoodsList();
  },
  mounted() {
    this.$nextTick(() => {
      this.Bodyscroll = new BScroll(this.$refs.wrapper, {
        movable: true,
        zoom: true,
        click: true,
        probeType: 3,
        // bounce: false, //取消下拉回弹效果
      });
      this.Bodyscroll.on("scroll", (pos) => {
        let posY = Math.abs(pos.y);
        let opacity = posY / 200;
        opacity = opacity > 1 ? 1 : opacity;
        this.styleOption.opacity = opacity;
        if (posY >= 100) {
          this.isShow = false;
        } else {
          this.isShow = true;
        }
      });
    });
  },
  activated() {
    if (this.$route.query.id != this.goodsId) {
      this.getGoodsList();
      this.goodsId = this.$route.query.id;
    }
  },
  methods: {
    backNextPage() {
      this.$router.back();
    },
    async getGoodsList() {
      let id = this.goodsId;
      let res = await getGoodslistId(id);
      if (res.data.code == 200) {
        this.newSwiperList = res.data.data;
      }
    },
    //加入购物车回调
    async addShopCat() {
      let isLogin = JSON.parse(localStorage.getItem("teaUserInfo"));
      if (!isLogin || !isLogin.success) {
        this.$router.push("/login");
      } else {
        let res = await addShopCart(this.goodsId);
        if (res.data.code == 200) {
          Toast.success("加入购物车成功");
        }
      }
    },
  },
};
</script>

<style scoped lang="less">
.detail {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
section {
  flex: 1;
  overflow: hidden;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 1.2rem;
  z-index: 999;
  .header-returns {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    i {
      width: 0.93rem;
      height: 0.93rem;
      margin: 0 0.3rem;
      color: #fff;
      background-color: rgba(0, 0, 0, 0.3);
      border-radius: 50%;
      font-size: 0.6rem;
      text-align: center;
      line-height: 0.93rem;
    }
  }
  .header-bar {
    width: 100%;
    height: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
    padding: 0 0.3rem;
    text-align: center;
    i {
      flex: 1;
      font-size: 0.6rem;
    }
    span {
      flex: 2;
      font-size: 0.45rem;
      font-weight: 500;
      color: #666;
    }
  }
}
.heightSpan {
  color: rgb(237, 107, 107) !important;
}
.goode-img {
  margin-top: 1rem;
  h4 {
    width: 100%;
    font-size: 0.7rem;
    font-weight: 500;
    text-align: center;
    margin-bottom: 0.5rem;
  }
  img {
    width: 100%;
    height: 5rem;
  }
}
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 1.5rem;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  ul {
    height: 100%;
    display: flex;
    align-items: center;
    li {
      flex: 1;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      border-right: 1px solid #ddd;
      i {
        font-size: 0.5rem;
      }
      span {
        font-size: 0.4rem;
      }
    }
  }
  .addShopCat {
    flex: 2;
    width: 35%;
    background-color: orange;
    color: #fff;
    font-size: 0.6rem;
  }
  .buyNow {
    flex: 2;
    width: 35%;
    background-color: red;
    color: #fff;
    font-size: 0.6rem;
  }
}
.swiper {
  position: relative;
}
.swiper-main {
  width: 100%;
  height: 10rem;
}
.swiper-container {
  width: 100%;
  height: 10rem;
}
#swiper-img {
  width: 100%;
  height: 10rem;
}
.swiper-pagination {
  position: absolute;
  width: 100%;
  bottom: 20px;
  color: #fff;
  font-size: 0.4rem;
}
::v-deep .swiper-pagination-bullet-active {
  background-color: #b0352f;
}
.goods-name {
  padding: 0.5rem 0.26rem;
  h1 {
    font-size: 0.55rem;
    font-weight: 500;
  }
  span {
    font-size: 0.35rem;
    color: #999;
  }
}
.goods-price {
  padding: 0.5rem 0.26rem;
  border-bottom: 1px solid #ccc;
  .oprice {
    color: red;
    font-size: 0.5rem;
  }
  .pprice {
    font-size: 0.4rem;
    color: #999;
    text-decoration: line-through;
  }
}
</style>
